import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";

import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IDashboardDataModel, IDashboardToDisplay } from "app/system-setup/dashboard-setup/models/dashboard";
import {
  IDashboardPropertyModel,
  IDashboardPropertyRequest,
} from "app/system-setup/dashboard-setup/models/dashboard-properties";
import { DashboardPropertiesService } from "app/system-setup/dashboard-setup/services/dashboard-properties.service";
import { DashboardService } from "app/system-setup/dashboard-setup/services/dashboard.service";
import { IPropertyModel } from "../../models/properties";
import { dashboardPropertyDisplay } from "../../mapper/dashboard-properties";
import { CustomValidators } from "ngx-custom-validators";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { EDashboardPropertiesType } from "app/shared/enums/dashboard-properties-type.enum";
import { dashboardDisplay } from "../../mapper/dashboard";

@Component({
  selector: "app-dashboard-properties",
  templateUrl: "./dashboard-properties.component.html",
  styleUrls: ["./dashboard-properties.component.scss"],
})
export class DashboardPropertiesComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = ["Ser", "id", "property", "value", "actions"];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IDashboardPropertyModel>;
  isLoaded: boolean = false;

  dashboardId: number;
  currentDashboard: IDashboardToDisplay;

  selectedPropertyType: string;
  dashboardPropertiesType = EDashboardPropertiesType;

  constructor(
    public dashboardPropertiesService: DashboardPropertiesService,
    private dashboardService: DashboardService
  ) {
    super();
  }

  ngOnInit(): void {
    this.activatedRoute.paramMap.subscribe((params) => {
      this.dashboardId = Number(params.get("id")) ?? 0;
    });

    this.form = this.fb.group({
      id: [0, {nonNullable: true}],
      propertyId: [null, [Validators.required]],
      value: [null, [Validators.required]],
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId() {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.dashboardId && this.checkNoBusinessOrCompany()) {
      this.getCurrentDashboard();
      this.getList(this.firstPage);
    }
  }

  getCurrentDashboard() {
    this.dashboardService
      .getById(this.dashboardId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IDashboardDataModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.currentDashboard = dashboardDisplay(res.data)[0];
        }
        else this.errorList(res?.message?.messages);
      });
  }

  getList(page: number, pageSize?: number) {
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.dashboardPropertiesService
      .getList(this.dashboardId, params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IDashboardPropertyModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            dashboardPropertyDisplay(res.data)
          );
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
          this.page.meta = res.meta;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IDashboardPropertyRequest = {
        dashboard_ID: this.dashboardId,
        // id: this.form.value.id,
        property_ID: this.form.value.propertyId,
        value: this.form.value.value,
      };
      if (this.selectedPropertyType == EDashboardPropertiesType.number)
        model.value = JSON.stringify(model.value);

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IDashboardPropertyRequest) {
    this.showLoading();
    this.dashboardPropertiesService
      .create(model)
      .subscribe((res: IResponse<IDashboardPropertyModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.firstPage);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  onDelete(dashboardProperties: IDashboardPropertyModel): void {
    let msg = dashboardProperties.property;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(dashboardProperties);
    });
  }

  delete(dashboardProperties: IDashboardPropertyModel) {
    this.showLoading();
    this.dashboardPropertiesService
      .Delete(dashboardProperties.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            // if last item in list: get previous page
            this.getList(this.prevPage, this.page.pageSize);
          }
          else {
            // if not last item in list: get current page
            this.getList(this.currentPage, this.page.pageSize);
          }
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }

  onChangeProperty(values: IPropertyModel[]) {
    // change value input type (number or text)
    let newSelectedType = values.length > 0 ? values[0]?.property_Type : null;
    if (newSelectedType != this.selectedPropertyType) {
      this.formCtrls.value.reset();

      if (newSelectedType == EDashboardPropertiesType.number) {
        this.formCtrls.value.setValidators([
          Validators.required,
          CustomValidators.number,
        ]);
      }
      else
        this.formCtrls.value.setValidators([
          Validators.required,
          CustomValidator.whiteSpace,
          CustomValidator.atLeastOneCharacter,
        ]);
      this.formCtrls.value.updateValueAndValidity();

      this.selectedPropertyType = newSelectedType;
    }
  }
}
