import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IDashboardPropertyModel,
  IDashboardPropertyRequest,
} from "../models/dashboard-properties";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class DashboardPropertiesService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IDashboardPropertyRequest) {
    return this.post<IResponse<IDashboardPropertyModel>>(
      "Dashboard/DashboardProperty/Create",
      data
    );
  }

  getList(dashboardId: number, params: IPageParams) {
    return this.get<IResponseList<IDashboardPropertyModel[]>>(
      "Dashboard/DashboardProperty/GetList",
      {
        params: params
      }
    );
  }

  getAll(dashboardId: number) {
    return this.get<IResponseList<IDashboardPropertyModel[]>>(
      "Dashboard/DashboardProperty/GetAll",
      {
        params: {
          $filter: `dashboard_ID eq ${dashboardId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Dashboard/DashboardProperty/Delete", {
      params: { Id: Id },
    });
  }
}
