import { msgTranslate } from "app/shared/resources/helpers-functions";
import { IDashboardPropertyModel } from "../models/dashboard-properties";

export function dashboardPropertyDisplay(
  data: IDashboardPropertyModel[]
): IDashboardPropertyModel[] {
  return data.map((ele) => {
    return <IDashboardPropertyModel>{
      ...ele,
      property: msgTranslate(
        ele.properties?.ar_Name,
        ele.properties?.en_Name
      ),
    };
  });
}
