import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { pathMatch } from "app/shared/enums/route-path-match.enum";
import { ViewsManager } from "app/shared/resources/views-manager";
import { DashboardPropertiesComponent } from "./components/dashboard-properties/dashboard-properties.component";
import { DashboardComponent } from "./components/dashboard/dashboard.component";
import { CheckIsViewAllowedGuard } from "app/shared/Guards/check-is-allowd.guard";
import { PropertiesComponent } from "app/system-setup/dashboard-setup/components/properties/properties.component";
import { DashboardDisplayComponent } from "app/system-setup/dashboard-setup/components/dashboard-dispaly/dashboard-display.component";

const routes: Routes = [
  {
    path: "dashboard",
    data: {view: ViewsManager.dashboard_setup},
    children: [
      {
        path: "",
        pathMatch: pathMatch.full,
        component: DashboardComponent,
        data: {view: ViewsManager.dashboard_setup},
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "dashboard-properties/:id",
        component: DashboardPropertiesComponent,
        data: {view: ViewsManager.dashboard_properties},
        canActivate: [CheckIsViewAllowedGuard],
      },
      // {
      //   path: "module-dashboard/:dashboardModuleId",
      //   component: DashboardDisplayComponent,
      //   data: {view: ViewsManager.module_dashboard},
      //   canActivate: [CheckIsViewAllowedGuard],
      // },
      {
        path: "home-dashboard",
        component: DashboardDisplayComponent,
        data: {view: ViewsManager.home_dashboard},
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },
  {
    path: "properties",
    component: PropertiesComponent,
    data: {view: ViewsManager.properties},
    canActivate: [CheckIsViewAllowedGuard],
  },
];

@NgModule({
  declarations: [],
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class DashboardSetupRoutingModule {}
