import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import {
  IDashboardDataModel,
  IDashboardModel,
  IDashboardModuleRequest,
  IDashboardRequest,
  IDashboardToDisplay,
} from "app/system-setup/dashboard-setup/models/dashboard";
import { DashboardService } from "app/system-setup/dashboard-setup/services/dashboard.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";
import { dashboardDisplay } from "../../mapper/dashboard";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";

@Component({
  selector: "app-dashboard",
  templateUrl: "./dashboard.component.html",
  styleUrls: ["./dashboard.component.scss"],
})
export class DashboardComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;
  removeFiles: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    "id",
    "en_Name",
    "ar_Name",
    "module",
    "link",
    "actions",
  ];
  isLoaded: boolean = false;
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IDashboardToDisplay>;
  constructor(public dashboardService: DashboardService) {
    super();
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      ar_Name: [null, CustomValidator.whiteSpace],
      en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      link: [
        null,
        [Validators.required, CustomValidator.whiteSpace, CustomValidators.url],
      ],
      module_Id: [null, Validators.required],
      service_Id: [null],
      isExistInHome: [false],
      home_Order: [null, CustomValidators.gt(0)],
      file: [null, Validators.required],
      dashboard_Order: [null, CustomValidators.gt(0)],
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
  }

  getList(page: number, pageSize?: number) {
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.dashboardService
      .getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IDashboardDataModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(dashboardDisplay(res.data));
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
          this.page.meta = res.meta;
        } else this.errorList(res?.message?.messages);
      });
  }

  submit() {
    this.removeFiles = false;
    this.submitted = true;

    if (this.form.valid) {
      const model = {

        dash_dashboard: {
          ID: this.formCtrls.id.value,
          Ar_Name: this.formCtrls.ar_Name.value,
          En_Name: this.formCtrls.en_Name.value,
          Link: this.formCtrls.link.value,
          Home_Order: this.formCtrls.home_Order.value??0,
          IsExistInHome:this.formCtrls.isExistInHome.value,
        } as IDashboardRequest,

        Module_ID: this.formCtrls.module_Id.value,
        Service_ID: this.formCtrls.service_Id.value??0,
        Dashboard_Order: this.formCtrls.dashboard_Order.value,
        file: this.formCtrls.file.value,
      } as IDashboardModuleRequest;

      let formData = HelpersFunctions.objectToFormData(model);

      model.dash_dashboard.ID == 0
        ? this.create(formData)
        : this.update(formData);
    }
  }

  create(model: FormData) {
    this.showLoading();
    this.dashboardService
      .create(model)
      .subscribe((res: IResponse<IDashboardModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.getList(this.firstPage, this.page.pageSize);
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  update(model: FormData) {
    this.showLoading();
    this.dashboardService
      .update(model)
      .subscribe((res: IResponse<IDashboardModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.getList(this.currentPage, this.page.pageSize);
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  itemToEdit(item: IDashboardToDisplay) {

    this.form.patchValue(item);
    this.formCtrls.file.setValue({
      id: item.id,
      name: item.en_Name,
      path: "/" + item?.file?.path,
      size: item.file?.size,
      type: item.file?.mime,
    })

    console.log('edit', this.formCtrls.file.value)

  }

  onDelete(item: IDashboardModel): void {
    let msg = this.msgTranslate(item.ar_Name, item.en_Name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(item);
    });
  }

  delete(item: IDashboardModel) {
    this.showLoading();
    this.dashboardService.Delete(item.id).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);

        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          this.getList(this.prevPage, this.page.pageSize);
        } else {
          this.getList(this.currentPage, this.page.pageSize);
        }
        if (item.id == this.formCtrls.id.value) this.clearForm();
      } else {
        this.errorList(res?.message?.messages);
      }
    });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
    this.removeFiles = true;
  }

  changeHomeOrderValidation() {
    if (this.formCtrls.isExistInHome.value) {
      this.formCtrls.home_Order.addValidators(Validators.required);
    } else {
      this.formCtrls.home_Order.clearValidators();
      this.formCtrls.home_Order.setValue(null);
    }
  }

}
